<template lang="html">
  <span class="json-widget">
    <component :is="tag" :readonly="readonly" :edit="editMode" :value="intlVal"
               @input="input" />
    <template v-if="!readonly">
      <vk-btn class="json-type-chooser" type="light">
        <font-awesome-icon icon="random" />
      </vk-btn>
      <vk-dropdown :delay-show="100" :offset="2">
        <vk-btn v-for="( a, i ) in actions" :key="i" size="small"
                @click.prevent="setType(a)">{{ a.label }}</vk-btn>
      </vk-dropdown>
    </template>
  </span>
</template>

<script lang="js">
import isPlainObject from 'lodash/isPlainObject'
import isArray from 'lodash/isArray'
import isNumber from 'lodash/isNumber'
import isString from 'lodash/isString'
import isBoolean from 'lodash/isBoolean'
import isNull from 'lodash/isNull'
import { Button as VkBtn } from 'vuikit/lib/button'
import { Dropdown as VkDropdown } from 'vuikit/lib/dropdown'
import components from './json'
import { library } from '@fortawesome/fontawesome-svg-core'
import { faRandom } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'

library.add( faRandom );

export default  {
  name: 'JsonWidget',
  components: {
    VkBtn,
    VkDropdown,
    FontAwesomeIcon,
    ...components
  },
  props: {
    value: {
      type: [ Object, Array, Number, String, Boolean ],
      default: null
    },
    readonly: {
      type: Boolean,
      default: true
    },
    edit: {
      type: Boolean,
      default: false
    }
  },
  mounted() {
    this.updateVal();
    this.editMode = this.edit;
  },
  data() {
    return {
      intlVal: null,
      types: {
        object: {
          label: 'Object',
          symbol: '{}',
        },
        array: {
          label: 'Array',
          symbol: '[]',
        },
        number: {
          label: 'Number',
          symbol: '#',
        },
        string: {
          label: 'String',
          symbol: '""',
        },
        boolean: {
          label: 'Boolean',
          symbol: '&',
        },
        null: {
          label: 'Null',
          symbol: '()',
        },
        invalid: {
          label: 'Invalid',
          symbol: '!',
        },
        unknown: {
          label: 'Unknown',
          symbol: '?',
        }
      },
      type: 'null',
      editMode: false,
    }
  },
  methods: {
    getType(v) {
      return (
        isPlainObject(v) ? 'object'
           : isArray(v) ? 'array'
           : isNumber(v) ? 'number'
           : isString(v) ? 'string'
           : isBoolean(v) ? 'boolean'
           : isNull(v) ? 'null'
           : 'invalid'
      );
    },
    updateVal() {
      const v = this.value;
      this.type = this.getType(v);
      this.intlVal = v;
    },
    setType( action ) {
      this.type = action.type;
      const v = action.op( this.intlVal );
      if ( this.type === 'unknown' )
        this.type = this.getType(v);
      this.intlVal = v;
      this.$emit( 'input', this.intlVal );
    },
    toObjectKey( val ) {
      var o = {}
      o[ val ] = null;
      return o;
    },
    input( val ) {
      this.intlVal = val;
      this.$emit( 'input', val );
    }
  },
  computed: {
    editing() {
      return !this.readonly && this.editMode;
    },
    tag() {
      return 'json-' + this.type;
    },
    actions() {
      const t = this.type;
      return [].concat(
        t == 'object' ? [
          {
            label: 'to object value',
            type: 'object',
            op: x => ({ '': x }),
          },
          {
            label: 'clear',
            type: 'object',
            op: () => ({}),
          },
          {
            label: 'to array element',
            type: 'array',
            op: x => [x],
          },
          {
            label: 'array of keys',
            type: 'array',
            op: Object.keys,
          },
          {
            label: 'array of values',
            type: 'array',
            op: Object.values,
          },
          {
            label: 'array of [key, val]',
            type: 'array',
            op: x => Object.keys(x).map( k => [ k, x[k] ]),
          },
          {
            label: 'array of {key, value}',
            type: 'array',
            op: x => Object.keys(x).map( k => ({ key:k, value: x[k] })),
          },
          {
            label: 'array',
            type: 'array',
            op: () => [],
          },
          {
            label: 'to entry count',
            type: 'number',
            op: x => Object.keys(x).length,
          },
          {
            label: 'number',
            type: 'number',
            op: () => 0,
          },
          {
            label: 'serialize',
            type: 'string',
            op: JSON.stringify,
          },
          {
            label: 'string',
            type: 'string',
            op: () => '',
          },
          {
            label: 'true',
            type: 'boolean',
            op: () => true,
          },
          {
            label: 'false',
            type: 'boolean',
            op: () => false,
          },
          {
            label: 'null',
            type: 'null',
            op: () => null,
          },
        ]
        : t == 'array' ? [
          {
            label: 'to object value',
            type: 'object',
            op: x => ({ '': x }),
          },
          {
            label: 'object',
            type: 'object',
            op: () => ({}),
          },
          {
            label: 'to array element',
            type: 'array',
            op: x => [x],
          },
          {
            label: 'clear',
            type: 'array',
            op: () => [],
          },
          {
            label: 'to length',
            type: 'number',
            op: x => x.length,
          },
          {
            label: 'number',
            type: 'number',
            op: () => 0,
          },
          {
            label: 'serialize',
            type: 'string',
            op: JSON.stringify,
          },
          {
            label: 'string',
            type: 'string',
            op: () => '',
          },
          {
            label: 'true',
            type: 'boolean',
            op: () => true,
          },
          {
            label: 'false',
            type: 'boolean',
            op: () => false,
          },
          {
            label: 'null',
            type: 'null',
            op: () => null,
          },
        ]
        : t == 'number' ? [
          {
            label: 'to object value',
            type: 'object',
            op: x => ({ '': x }),
          },
          {
            label: 'to object key',
            type: 'object',
            op: this.toObjectKey,
          },
          {
            label: 'object',
            type: 'object',
            op: () => ({}),
          },
          {
            label: 'to array element',
            type: 'array',
            op: x => [x],
          },
          {
            label: 'array',
            type: 'array',
            op: () => [],
          },
          {
            label: 'serialize',
            type: 'string',
            op: JSON.stringify,
          },
          {
            label: 'string',
            type: 'string',
            op: () => '',
          },
          {
            label: 'true',
            type: 'boolean',
            op: () => true,
          },
          {
            label: 'false',
            type: 'boolean',
            op: () => false,
          },
          {
            label: 'null',
            type: 'null',
            op: () => null,
          },
        ]
        : t == 'string' ? [
          {
            label: 'to object value',
            type: 'object',
            op: x => ({ '': x }),
          },
          {
            label: 'to object key',
            type: 'object',
            op: this.toObjectKey,
          },
          {
            label: 'object',
            type: 'object',
            op: () => ({}),
          },
          {
            label: 'to array element',
            type: 'array',
            op: x => [x],
          },
          {
            label: 'array',
            type: 'array',
            op: () => [],
          },
          {
            label: 'to int',
            type: 'number',
            op: parseInt,
          },
          {
            label: 'to float',
            type: 'number',
            op: parseFloat,
          },
          {
            label: 'to length',
            type: 'number',
            op: x => x.length,
          },
          {
            label: 'number',
            type: 'number',
            op: () => 0,
          },
          {
            label: 'escape',
            type: 'string',
            op: escape,
          },
          {
            label: 'encode URI',
            type: 'string',
            op: encodeURI,
          },
          {
            label: 'encode URI component',
            type: 'string',
            op: encodeURIComponent,
          },
          {
            label: 'unescape',
            type: 'string',
            op: unescape,
          },
          {
            label: 'decode URI',
            type: 'string',
            op: decodeURI,
          },
          {
            label: 'decode URI component',
            type: 'string',
            op: decodeURIComponent,
          },
          {
            label: 'string',
            type: 'string',
            op: () => '',
          },
          {
            label: 'parse json',
            type: 'unknown',
            op: JSON.parse,
          },
          {
            label: 'true',
            type: 'boolean',
            op: () => true,
          },
          {
            label: 'false',
            type: 'boolean',
            op: () => false,
          },
          {
            label: 'null',
            type: 'null',
            op: () => null,
          },
        ]
        : t == 'boolean' ? [
          {
            label: 'to object value',
            type: 'object',
            op: x => ({ '': x }),
          },
          {
            label: 'to object key',
            type: 'object',
            op: this.toObjectKey,
          },
          {
            label: 'object',
            type: 'object',
            op: () => ({}),
          },
          {
            label: 'to array element',
            type: 'array',
            op: x => [x],
          },
          {
            label: 'array',
            type: 'array',
            op: () => [],
          },
          {
            label: 'number',
            type: 'number',
            op: () => 0,
          },
          {
            label: 'serialize',
            type: 'string',
            op: JSON.stringify,
          },
          {
            label: 'string',
            type: 'string',
            op: () => '',
          },
          this.intlVal === false ? {
            label: 'true',
            type: 'boolean',
            op: () => true,
          }
          : {
            label: 'false',
            type: 'boolean',
            op: () => false,
          },
          {
            label: 'null',
            type: 'null',
            op: () => null,
          },
        ]
        :[
          {
            label: 'to object value',
            type: 'object',
            op: x => ({ '': x }),
          },
          {
            label: 'to object key',
            type: 'object',
            op: this.toObjectKey,
          },
          {
            label: 'object',
            type: 'object',
            op: () => ({}),
          },
          {
            label: 'to array element',
            type: 'array',
            op: x => [x],
          },
          {
            label: 'array',
            type: 'array',
            op: () => [],
          },
          {
            label: 'number',
            type: 'number',
            op: () => 0,
          },
          {
            label: 'serialize',
            type: 'string',
            op: JSON.stringify,
          },
          {
            label: 'string',
            type: 'string',
            op: () => '',
          },
          {
            label: 'true',
            type: 'boolean',
            op: () => true,
          },
          {
            label: 'false',
            type: 'boolean',
            op: () => false,
          },
          {
            label: 'null',
            type: 'null',
            op: () => null,
          },
        ]
      ).filter( x => x.op );
    }
  },
  watch: {
    value( val ) {
      this.updateVal();
    },
    edit( val ) {
      this.editMode = val;
    }
  }
}
</script>

<style lang="scss">
.json-widget {
  line-height: 1.4em;
  .json-delim {
    font-weight: bold;
    color: midnightblue;
  }
  .json-collapse {
    line-height: 12px;
    padding: 0 4px;
    font-size: 12px;
  }
  .uk-button {
    &.json-type-chooser, &.json-add {
      padding: 2px 6px 3px;
      line-height: 13px;
      font-size: 13px;
      font-weight: bold;
      border-radius: 3px;
    }
    &.json-type-chooser {
      margin-right: 2px;
      background: transparent;
    }
  }
  > .uk-dropdown {
    padding: 0;
    > * {
      display: block;
      width: 100%;
      padding: 2px 10px;
      line-height: unset;
    }
  }
  .json-ellipses {
    font-weight: bold;
    margin: 0 2px;
  }
}
</style>
